
.category-preview-container {
  display: flex;
  position: relative;
  flex-direction: column;
  box-sizing: border-box;
}
  .merch-card-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: stretch;
    border-radius: 0.3rem;
    padding: 0.8rem;
    background-color: rgb(248, 248, 248);
    box-shadow: rgba(0, 0, 0, 0.23) 0 8px 9px;
    transition: 0.3s;

    &:hover {
      scale: 0.995;
      box-shadow: rgba(0, 0, 0, 0.23) 0 3px 4px;
    }


    .footer {
      display: flex;
      align-items: center;
      justify-content: space-around;
      width: calc(100% - 0.8rem * 2);
      left: 0;
      right: 0;
      bottom: 1rem;
      margin: 0 auto;
      height: 10vh;
      position: absolute;
      text-transform: uppercase;
      font-weight: bold;
      color: white;
      backdrop-filter: blur(1px);
      border-radius: 3px;
      background-image: linear-gradient(to top, rgba(0, 0, 0, 0.46), rgba(255, 255, 255, 0));
    }

    &:hover {
      button {
        color: white;
        backdrop-filter: blur(1.5px);
      }
    }

    img {
      width: calc(25vw - 0.8rem * 2 - 5px);
      object-fit: cover;
      background-color: #F8F8F8F8;
    }

    button {
      position: absolute;
      background-color: transparent;
      color: transparent;
      border: none;
      transition: 1s;
      border-radius: 10%;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;

      &:hover {
        color: white;
        backdrop-filter: blur(1.5px);
      }

    }
  }

  h2 {
    text-align: left;
    padding: 1rem 4rem;
    font-size: 28px;
    cursor: pointer;
  }

  .preview {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 0.3125rem;
  }

  .price {
    font-size: 1.3rem;
  }



@media (min-width: 768px) and (max-width: 1280px) {
  .category-preview-container {
    .merch-card-container {
      img {
        width: calc(33vw - 0.8rem * 2);
      }
    }
    .preview {
      grid-template-columns: repeat(3, 1fr);
    }
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .category-preview-container {
    margin-top: 4rem;
    .merch-card-container {
      img {
        width: calc(50vw - 0.8rem * 2);
      }
    }
    .preview {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

@media (max-width: 481px) {
  .category-preview-container {
    .merch-card-container {
      img {
        width: calc(100vw - 0.8rem * 2);
      }
    }
    .preview {
      grid-template-columns: repeat(1, 1fr);
    }

    h2 {
      padding-left: 5rem;
    }
  }
}


