.spinner-overlay {
  height: 100vh;
  display: grid;
  place-content: center;

  .spinner {
    width: 10rem;
    height: 10rem;
    //border-style: solid;
    position: relative;
    background-color: transparent;

    span {
      position: absolute;
      top: 2.5rem;
      left: .35rem;
      text-transform: uppercase;
      font-weight: bold;
      color: rgba(85, 96, 110, 0.76);
      filter: blur(0.5px);
    }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      border: 4px solid;
      border-radius: 100%;
      width: 102px;
      background-color: transparent;
    };
    &::before {
      width: 6rem;
      height: 6rem;
      border-color: rgba(85, 96, 110, 0.76) transparent;
      animation: spins 2s linear infinite;
    };
    &::after {
      top: 1rem;
      left: 1rem;
      width: 4rem;
      height: 4rem;
      border-color: transparent rgba(85, 96, 110, 0.76);
      animation: spins 1s linear infinite reverse;
    };


    @keyframes spins {
      0% {
        transform: rotate(0deg);
      }
      50% {
        transform: rotate(180deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
  }

}